html,
body {
    font-family: 'Roboto', 'Helvetica', sans-serif;
    margin: 0;
    padding: 0;
}

.mdl-layout {
    height: 100vh;
}

.mdl-grid {
    min-height: calc(100vh - 56px);
    box-sizing: border-box;
}
.news-item { 
    .news-item__title {
        color: #fff;
        background-color: #46B6AC;
        padding: 10px;
        .news-item__title-text {
            margin-bottom: 5px;
        }
        .news-item__title-time {
            font-size: 12px;
        }
    }
    .news-item__content {
        padding: 5px;
        width: 100%;
        box-sizing: border-box;
        img {
            width: calc(100% + 10px);
            height: auto;
            padding: 0;
            margin-bottom: 5px;
            margin-left: -5px;
            margin-top: -5px;
        }
    }
    .news-item__actions {
        overflow: initial;
        > *:last-child {
            float: right;
        }
    }
}

.mdl-layout__content {
    padding: 1em;
}

/** masonry layout **/

    .masonry {
        width: 100%;
        -moz-column-gap: 1em;
        -webkit-column-gap: 1em;
        column-gap: 1em;
        -moz-column-count: 1;
        -webkit-column-count: 1;
        column-count: 1;
        > div {
            display: inline-block;
            width: 100%;
            margin-bottom: 1em;
        }
    }
    @media only screen and (min-width: 413px) {
        .masonry {
            -moz-column-count: 2;
            -webkit-column-count: 2;
            column-count: 2;
        }
    }
    @media only screen and (min-width: 768px) {
        .masonry {
            -moz-column-count: 3;
            -webkit-column-count: 3;
            column-count: 3;
        }
    }
    @media only screen and (min-width: 992px) {
        .masonry {
            -moz-column-count: 4;
            -webkit-column-count: 4;
            column-count: 4;
        }
    }
    @media only screen and (min-width: 1080px) {
        .masonry {
            -moz-column-count: 5;
            -webkit-column-count: 5;
            column-count: 5;
        }
    }

/** popup **/

.dialog-container,
.loading-container {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    overflow: scroll;
    background: rgba(0, 0, 0, 0.4);
    z-index: 99!important;
    opacity: 0;
    -webkit-transition: opacity 400ms ease-in;
    -moz-transition: opacity 400ms ease-in;
    transition: opacity 400ms ease-in;
}

.dialog-container {
    > div {
        position: relative;
        width: 90%;
        max-width: 1000px;
        min-height: 25px;
        margin: 20px auto;
        z-index: 99!important;
        padding: 16px 16px 0;
    }
    img {
        width: 100%;
        height: auto;
    }
    table {
        width: 100%;
    }
    .caption {
        font-size: 12px;
        color: #7f7f7f;
        background-color: #ededed;
        padding: 3px 5px;
    }
}

.dialog-button-bar {
    text-align: right;
    margin-top: 8px;
}

.loading-container {
    > div {
        position: relative;
        width: 50px;
        height: 50px;
        margin: 10% auto;
        z-index: 99999;
        > div {
            width: 100%;
            height: 100%;
        }
    }
}
